.TopBar{
  background-color: rgb(218, 218, 218);
  height: 6vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.TopSelect select{
   width: 6vw;
   margin-left: .5vw;
}
.parent {
  position: relative;
  width: 100%;
  height: 100%;
}

.container {
  padding: 10px;
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
}

.bar-chart {
  display: flex;
  align-items: center;
  border: 1px solid #ccc;
  justify-content: center;
}
.radioController{
  margin-left: 25%;
}



.corr-matrix-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid #ccc;
}

.corr-matrix {
  margin-top: 1rem;
  margin-bottom: 8rem;
}

.axis text {
  font: 10px sans-serif;
}

.axis path,
.axis line {
  fill: none;
  stroke: #000;
  shape-rendering: crispEdges;
}

.legend {
  font-family: sans-serif;
  font-size: 12px;
}
.scatterPlot-container{
  display: flex;
  flex-direction: column;
  align-items: center;
}